import React, { useState, useEffect } from 'react';
import { useAuth } from '../../hooks';
import { TeamLogo } from '../../utils/imageUtils';

/**
 * Comprehensive Bracket Management Component
 * Full CRUD operations for tournament brackets
 */
function BracketManagement({ 
  event, 
  teams = [], 
  onUpdate,
  navigateTo
}) {
  const { api } = useAuth();
  const [loading, setLoading] = useState(false);
  const [bracket, setBracket] = useState(null);
  const [activeTab, setActiveTab] = useState('overview');
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  
  // Bracket configuration state
  const [bracketConfig, setBracketConfig] = useState({
    format: event?.format || 'single_elimination',
    seeding_method: 'rating',
    shuffle_seeds: false,
    best_of: 3,
    third_place_match: false
  });

  // Fetch existing bracket
  useEffect(() => {
    if (event?.id) {
      fetchBracket();
    }
  }, [event?.id]);

  const fetchBracket = async () => {
    try {
      setLoading(true);
      const response = await api.get(`/admin/events/${event.id}/bracket`);
      if (response.data?.data) {
        setBracket(response.data.data);
      }
    } catch (error) {
      console.log('No bracket exists yet');
      setBracket(null);
    } finally {
      setLoading(false);
    }
  };

  // Generate new bracket
  const handleGenerateBracket = async () => {
    if (teams.length < 2) {
      setError('Need at least 2 teams to generate a bracket');
      return;
    }

    try {
      setLoading(true);
      setError('');
      
      const response = await api.post(`/admin/events/${event.id}/generate-bracket`, {
        format: bracketConfig.format,
        seeding_method: bracketConfig.seeding_method,
        shuffle_seeds: bracketConfig.shuffle_seeds,
        best_of: bracketConfig.best_of,
        third_place_match: bracketConfig.third_place_match
      });

      if (response.data?.success) {
        setSuccess('Bracket generated successfully!');
        setBracket(response.data.data);
        await fetchBracket();
        if (onUpdate) onUpdate();
      }
    } catch (error) {
      setError(error.response?.data?.message || 'Failed to generate bracket');
    } finally {
      setLoading(false);
    }
  };

  // Update existing bracket
  const handleUpdateBracket = async () => {
    try {
      setLoading(true);
      setError('');
      
      const response = await api.put(`/admin/events/${event.id}/bracket`, {
        format: bracketConfig.format,
        seeding_method: bracketConfig.seeding_method,
        best_of: bracketConfig.best_of,
        third_place_match: bracketConfig.third_place_match
      });

      if (response.data?.success) {
        setSuccess('Bracket updated successfully!');
        setBracket(response.data.data);
        if (onUpdate) onUpdate();
      }
    } catch (error) {
      setError(error.response?.data?.message || 'Failed to update bracket');
    } finally {
      setLoading(false);
    }
  };

  // Delete bracket
  const handleDeleteBracket = async () => {
    if (!window.confirm('Are you sure you want to delete the bracket? This will remove all matches.')) {
      return;
    }

    try {
      setLoading(true);
      setError('');
      
      const response = await api.delete(`/admin/events/${event.id}/bracket`);
      
      if (response.data?.success) {
        setSuccess('Bracket deleted successfully!');
        setBracket(null);
        if (onUpdate) onUpdate();
      }
    } catch (error) {
      setError(error.response?.data?.message || 'Failed to delete bracket');
    } finally {
      setLoading(false);
    }
  };

  // Recreate bracket (delete and regenerate)
  const handleRecreateBracket = async () => {
    if (!window.confirm('This will delete the existing bracket and all matches, then create a new one. Continue?')) {
      return;
    }

    try {
      setLoading(true);
      setError('');
      
      // First delete
      await api.delete(`/admin/events/${event.id}/bracket`);
      
      // Then generate new
      const response = await api.post(`/admin/events/${event.id}/generate-bracket`, {
        format: bracketConfig.format,
        seeding_method: bracketConfig.seeding_method,
        shuffle_seeds: bracketConfig.shuffle_seeds,
        best_of: bracketConfig.best_of,
        third_place_match: bracketConfig.third_place_match
      });

      if (response.data?.success) {
        setSuccess('Bracket recreated successfully!');
        setBracket(response.data.data);
        await fetchBracket();
        if (onUpdate) onUpdate();
      }
    } catch (error) {
      setError(error.response?.data?.message || 'Failed to recreate bracket');
    } finally {
      setLoading(false);
    }
  };

  // Update match result
  const handleMatchUpdate = async (matchId, result) => {
    try {
      setLoading(true);
      const response = await api.put(`/admin/matches/${matchId}`, result);
      
      if (response.data?.success) {
        setSuccess('Match updated successfully!');
        await fetchBracket();
        if (onUpdate) onUpdate();
      }
    } catch (error) {
      setError(error.response?.data?.message || 'Failed to update match');
    } finally {
      setLoading(false);
    }
  };

  const tabs = [
    { key: 'overview', label: 'Overview', icon: '' },
    { key: 'generate', label: 'Generate', icon: '' },
    { key: 'matches', label: 'Matches', icon: '' },
    { key: 'settings', label: 'Settings', icon: '' }
  ];

  const formats = [
    { value: 'single_elimination', label: 'Single Elimination' },
    { value: 'double_elimination', label: 'Double Elimination' },
    { value: 'round_robin', label: 'Round Robin' },
    { value: 'swiss', label: 'Swiss System' },
    { value: 'groups', label: 'Group Stage' }
  ];

  const seedingMethods = [
    { value: 'rating', label: 'By Rating' },
    { value: 'manual', label: 'Manual Seeding' },
    { value: 'random', label: 'Random' }
  ];

  return (
    <div className="bracket-management">
      {/* Header */}
      <div className="mb-6">
        <div className="flex items-center justify-between">
          <h3 className="text-xl font-bold text-gray-900 dark:text-white">
            Bracket Management
          </h3>
          <div className="flex items-center gap-2">
            {bracket ? (
              <span className="px-3 py-1 bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200 rounded-full text-sm">
                 Bracket Active
              </span>
            ) : (
              <span className="px-3 py-1 bg-gray-100 text-gray-800 dark:bg-gray-800 dark:text-gray-200 rounded-full text-sm">
                No Bracket
              </span>
            )}
          </div>
        </div>
      </div>

      {/* Alerts */}
      {error && (
        <div className="mb-4 p-4 bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded-md">
          <p className="text-red-600 dark:text-red-400">{error}</p>
        </div>
      )}
      
      {success && (
        <div className="mb-4 p-4 bg-green-50 dark:bg-green-900/20 border border-green-200 dark:border-green-800 rounded-md">
          <p className="text-green-600 dark:text-green-400">{success}</p>
        </div>
      )}

      {/* Tab Navigation */}
      <div className="border-b border-gray-200 dark:border-gray-700 mb-6">
        <nav className="flex space-x-6">
          {tabs.map(tab => (
            <button
              key={tab.key}
              onClick={() => setActiveTab(tab.key)}
              className={`py-3 px-1 text-sm font-medium transition-colors relative ${
                activeTab === tab.key
                  ? 'text-red-600 dark:text-red-400 border-b-2 border-red-600 dark:border-red-400'
                  : 'text-gray-600 dark:text-gray-400 hover:text-gray-900 dark:hover:text-gray-200'
              }`}
            >
              <span className="flex items-center gap-2">
                <span>{tab.icon}</span>
                {tab.label}
              </span>
            </button>
          ))}
        </nav>
      </div>

      {/* Tab Content */}
      <div className="tab-content">
        {/* Overview Tab */}
        {activeTab === 'overview' && (
          <div className="space-y-6">
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
              <div className="bg-gray-50 dark:bg-gray-800 p-4 rounded-lg">
                <p className="text-sm text-gray-600 dark:text-gray-400">Format</p>
                <p className="text-lg font-semibold text-gray-900 dark:text-white">
                  {bracket?.format || bracketConfig.format || 'Not Set'}
                </p>
              </div>
              <div className="bg-gray-50 dark:bg-gray-800 p-4 rounded-lg">
                <p className="text-sm text-gray-600 dark:text-gray-400">Teams</p>
                <p className="text-lg font-semibold text-gray-900 dark:text-white">
                  {teams.length} / {event?.max_teams || 16}
                </p>
              </div>
              <div className="bg-gray-50 dark:bg-gray-800 p-4 rounded-lg">
                <p className="text-sm text-gray-600 dark:text-gray-400">Rounds</p>
                <p className="text-lg font-semibold text-gray-900 dark:text-white">
                  {bracket?.total_rounds || 0}
                </p>
              </div>
              <div className="bg-gray-50 dark:bg-gray-800 p-4 rounded-lg">
                <p className="text-sm text-gray-600 dark:text-gray-400">Matches</p>
                <p className="text-lg font-semibold text-gray-900 dark:text-white">
                  {bracket?.matches_count || 0}
                </p>
              </div>
            </div>

            {/* Quick Actions */}
            <div className="bg-gray-50 dark:bg-gray-800 p-6 rounded-lg">
              <h4 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">
                Quick Actions
              </h4>
              <div className="flex flex-wrap gap-3">
                {!bracket ? (
                  <button
                    onClick={handleGenerateBracket}
                    disabled={loading || teams.length < 2}
                    className="px-6 py-2 bg-green-600 text-white rounded-md hover:bg-green-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                  >
                    {loading ? 'Generating...' : ' Generate Bracket'}
                  </button>
                ) : (
                  <>
                    <button
                      onClick={handleUpdateBracket}
                      disabled={loading}
                      className="px-6 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                    >
                      {loading ? 'Updating...' : ' Update Bracket'}
                    </button>
                    <button
                      onClick={handleRecreateBracket}
                      disabled={loading}
                      className="px-6 py-2 bg-yellow-600 text-white rounded-md hover:bg-yellow-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                    >
                      {loading ? 'Recreating...' : ' Recreate Bracket'}
                    </button>
                    <button
                      onClick={handleDeleteBracket}
                      disabled={loading}
                      className="px-6 py-2 bg-red-600 text-white rounded-md hover:bg-red-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                    >
                      {loading ? 'Deleting...' : ' Delete Bracket'}
                    </button>
                    <button
                      onClick={() => navigateTo && navigateTo('admin-bracket-view', { id: event.id })}
                      className="px-6 py-2 bg-purple-600 text-white rounded-md hover:bg-purple-700 transition-colors"
                    >
                       View Bracket
                    </button>
                  </>
                )}
              </div>
            </div>

            {/* Teams List */}
            {teams.length > 0 && (
              <div className="bg-gray-50 dark:bg-gray-800 p-6 rounded-lg">
                <h4 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">
                  Registered Teams ({teams.length})
                </h4>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
                  {teams.map((team, index) => (
                    <div key={team.id} className="flex items-center gap-3 p-3 bg-white dark:bg-gray-700 rounded-lg">
                      <span className="text-sm font-semibold text-gray-500 dark:text-gray-400">
                        #{index + 1}
                      </span>
                      <TeamLogo 
                        team={team} 
                        size="small"
                        className="w-8 h-8 rounded"
                      />
                      <div className="flex-1">
                        <p className="font-medium text-gray-900 dark:text-white">
                          {team.name}
                        </p>
                        <p className="text-xs text-gray-500 dark:text-gray-400">
                          {team.region}  Rating: {team.rating || 'N/A'}
                        </p>
                      </div>
                      {team.pivot?.seed && (
                        <span className="px-2 py-1 bg-blue-100 text-blue-800 dark:bg-blue-900 dark:text-blue-200 rounded text-xs">
                          Seed {team.pivot.seed}
                        </span>
                      )}
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        )}

        {/* Generate Tab */}
        {activeTab === 'generate' && (
          <div className="space-y-6">
            <div className="bg-yellow-50 dark:bg-yellow-900/20 border border-yellow-200 dark:border-yellow-800 p-4 rounded-lg">
              <p className="text-yellow-800 dark:text-yellow-200">
                 {bracket ? 'Generating a new bracket will replace the existing one!' : 'Configure your bracket settings before generating.'}
              </p>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              {/* Format Selection */}
              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                  Tournament Format
                </label>
                <select
                  value={bracketConfig.format}
                  onChange={(e) => setBracketConfig({...bracketConfig, format: e.target.value})}
                  className="w-full px-4 py-2 border border-gray-300 dark:border-gray-600 rounded-md bg-white dark:bg-gray-800 text-gray-900 dark:text-white"
                >
                  {formats.map(format => (
                    <option key={format.value} value={format.value}>
                      {format.label}
                    </option>
                  ))}
                </select>
              </div>

              {/* Seeding Method */}
              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                  Seeding Method
                </label>
                <select
                  value={bracketConfig.seeding_method}
                  onChange={(e) => setBracketConfig({...bracketConfig, seeding_method: e.target.value})}
                  className="w-full px-4 py-2 border border-gray-300 dark:border-gray-600 rounded-md bg-white dark:bg-gray-800 text-gray-900 dark:text-white"
                >
                  {seedingMethods.map(method => (
                    <option key={method.value} value={method.value}>
                      {method.label}
                    </option>
                  ))}
                </select>
              </div>

              {/* Best Of */}
              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                  Match Format
                </label>
                <select
                  value={bracketConfig.best_of}
                  onChange={(e) => setBracketConfig({...bracketConfig, best_of: parseInt(e.target.value)})}
                  className="w-full px-4 py-2 border border-gray-300 dark:border-gray-600 rounded-md bg-white dark:bg-gray-800 text-gray-900 dark:text-white"
                >
                  <option value="1">Best of 1</option>
                  <option value="3">Best of 3</option>
                  <option value="5">Best of 5</option>
                  <option value="7">Best of 7</option>
                </select>
              </div>

              {/* Additional Options */}
              <div className="space-y-3">
                <label className="flex items-center gap-3">
                  <input
                    type="checkbox"
                    checked={bracketConfig.shuffle_seeds}
                    onChange={(e) => setBracketConfig({...bracketConfig, shuffle_seeds: e.target.checked})}
                    className="w-4 h-4 text-red-600 rounded focus:ring-red-500"
                  />
                  <span className="text-sm text-gray-700 dark:text-gray-300">
                    Shuffle Seeds (Randomize matchups)
                  </span>
                </label>

                {bracketConfig.format === 'single_elimination' && (
                  <label className="flex items-center gap-3">
                    <input
                      type="checkbox"
                      checked={bracketConfig.third_place_match}
                      onChange={(e) => setBracketConfig({...bracketConfig, third_place_match: e.target.checked})}
                      className="w-4 h-4 text-red-600 rounded focus:ring-red-500"
                    />
                    <span className="text-sm text-gray-700 dark:text-gray-300">
                      Include 3rd Place Match
                    </span>
                  </label>
                )}
              </div>
            </div>

            {/* Action Buttons */}
            <div className="flex gap-3 pt-4">
              <button
                onClick={bracket ? handleRecreateBracket : handleGenerateBracket}
                disabled={loading || teams.length < 2}
                className="px-6 py-3 bg-green-600 text-white rounded-md hover:bg-green-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors font-medium"
              >
                {loading ? 'Processing...' : bracket ? ' Regenerate Bracket' : ' Generate Bracket'}
              </button>
              
              {teams.length < 2 && (
                <p className="text-sm text-red-600 dark:text-red-400 py-3">
                   Add at least 2 teams to generate a bracket
                </p>
              )}
            </div>
          </div>
        )}

        {/* Matches Tab */}
        {activeTab === 'matches' && (
          <div className="space-y-6">
            {bracket?.matches && bracket.matches.length > 0 ? (
              <div className="space-y-4">
                <h4 className="text-lg font-semibold text-gray-900 dark:text-white">
                  Tournament Matches
                </h4>
                <div className="grid gap-4">
                  {bracket.matches.map(match => (
                    <div key={match.id} className="bg-gray-50 dark:bg-gray-800 p-4 rounded-lg">
                      <div className="flex items-center justify-between">
                        <div className="flex items-center gap-4">
                          <span className="text-sm text-gray-500 dark:text-gray-400">
                            Round {match.round}
                          </span>
                          <div className="flex items-center gap-2">
                            <span className="font-medium text-gray-900 dark:text-white">
                              {match.team1?.name || 'TBD'}
                            </span>
                            <span className="text-gray-500 dark:text-gray-400">vs</span>
                            <span className="font-medium text-gray-900 dark:text-white">
                              {match.team2?.name || 'TBD'}
                            </span>
                          </div>
                        </div>
                        <div className="flex items-center gap-2">
                          {match.status === 'completed' ? (
                            <span className="px-2 py-1 bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200 rounded text-xs">
                              Completed
                            </span>
                          ) : match.status === 'live' ? (
                            <span className="px-2 py-1 bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-200 rounded text-xs">
                              Live
                            </span>
                          ) : (
                            <span className="px-2 py-1 bg-gray-100 text-gray-800 dark:bg-gray-700 dark:text-gray-200 rounded text-xs">
                              Upcoming
                            </span>
                          )}
                          <button
                            onClick={() => navigateTo && navigateTo('admin-match-edit', { id: match.id })}
                            className="px-3 py-1 bg-blue-600 text-white rounded text-sm hover:bg-blue-700 transition-colors"
                          >
                            Edit
                          </button>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            ) : (
              <div className="text-center py-12 bg-gray-50 dark:bg-gray-800 rounded-lg">
                <p className="text-gray-500 dark:text-gray-400">
                  No matches yet. Generate a bracket to create matches.
                </p>
              </div>
            )}
          </div>
        )}

        {/* Settings Tab */}
        {activeTab === 'settings' && (
          <div className="space-y-6">
            <div className="bg-gray-50 dark:bg-gray-800 p-6 rounded-lg">
              <h4 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">
                Advanced Settings
              </h4>
              <div className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                    Auto-advance Winners
                  </label>
                  <select className="w-full px-4 py-2 border border-gray-300 dark:border-gray-600 rounded-md bg-white dark:bg-gray-700 text-gray-900 dark:text-white">
                    <option value="true">Enabled - Winners advance automatically</option>
                    <option value="false">Disabled - Manual advancement</option>
                  </select>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                    Match Check-in Required
                  </label>
                  <select className="w-full px-4 py-2 border border-gray-300 dark:border-gray-600 rounded-md bg-white dark:bg-gray-700 text-gray-900 dark:text-white">
                    <option value="false">No check-in required</option>
                    <option value="true">Teams must check-in before matches</option>
                  </select>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                    Default Match Time Limit
                  </label>
                  <input
                    type="number"
                    placeholder="Minutes (0 for no limit)"
                    className="w-full px-4 py-2 border border-gray-300 dark:border-gray-600 rounded-md bg-white dark:bg-gray-700 text-gray-900 dark:text-white"
                  />
                </div>
              </div>
            </div>

            {/* Danger Zone */}
            {bracket && (
              <div className="bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 p-6 rounded-lg">
                <h4 className="text-lg font-semibold text-red-800 dark:text-red-200 mb-4">
                  Danger Zone
                </h4>
                <p className="text-sm text-red-700 dark:text-red-300 mb-4">
                  These actions are irreversible and will affect all bracket data.
                </p>
                <div className="flex gap-3">
                  <button
                    onClick={handleDeleteBracket}
                    disabled={loading}
                    className="px-4 py-2 bg-red-600 text-white rounded-md hover:bg-red-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors text-sm"
                  >
                    Delete Entire Bracket
                  </button>
                  <button
                    onClick={() => {
                      if (window.confirm('Reset all match scores to 0-0?')) {
                        // Implement reset scores
                      }
                    }}
                    disabled={loading}
                    className="px-4 py-2 bg-red-600 text-white rounded-md hover:bg-red-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors text-sm"
                  >
                    Reset All Scores
                  </button>
                </div>
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
}

export default BracketManagement;